let windowScrollTop;

export function hideBodyScroll() {

  document.body.classList.add('no-scroll');

  windowScrollTop = window.pageYOffset; // Ñ‚ÐµÐºÑƒÑ‰Ð°Ñ Ð¿Ñ€Ð¾ÐºÑ€ÑƒÑ‚ÐºÐ° ÑÐ²ÐµÑ€Ñ…Ñƒ
  document.body.style.position = 'fixed';

  if (hasScrollbar()) {
    // Ñ ÑƒÑ‡ÐµÑ‚Ð¾Ð¼ Ð³Ð¾Ñ€Ð¸Ð·Ð¾Ð½Ñ‚Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ ÑÐºÑ€Ð¾Ð»Ð»Ð°. Ð§Ñ‚Ð¾Ð±Ñ‹ Ð½ÐµÐ±Ñ‹Ð»Ð¾ Ñ€Ñ‹Ð²ÐºÐ° Ð¿Ñ€Ð¸ Ð¾Ñ‚ÐºÑ€Ñ‹Ñ‚Ð¸Ð¸ Ð¼Ð¾Ð´Ð°Ð»ÑŒÐ½Ð¾Ð³Ð¾ Ð¾ÐºÐ½Ð°
    document.body.style.width = `calc(100% - ${getScrollbarSize()}px)`;
  } else {
    document.body.style.width = '100%';
  }
  document.body.style.top = -windowScrollTop + 'px';
}

function getScrollbarSize() { // Ð¿Ð¾Ð»ÑƒÑ‡ÐµÐ½Ð¸Ðµ ÑˆÐ¸Ñ€Ð¸Ð½Ñ‹ ÑÐºÑ€Ð¾Ð»Ð»Ð°
  let outer = document.createElement('div');
  outer.style.visibility = 'hidden';
  outer.style.width = '100px';
  outer.style.msOverflowStyle = 'scrollbar'; // needed for WinJS apps

  document.body.appendChild(outer);

  let widthNoScroll = outer.offsetWidth;
  // force scrollbars
  outer.style.overflow = 'scroll';

  let inner = document.createElement('div');
  inner.style.width = '100%';
  outer.appendChild(inner);

  let widthWithScroll = inner.offsetWidth;

  outer.parentNode.removeChild(outer);

  return widthNoScroll - widthWithScroll;
}

function hasScrollbar() { // Ð¿Ñ€Ð¾Ð²ÐµÑ€ÐºÐ° Ð½Ð° Ð±Ð¾ÐºÐ¾Ð²Ð¾Ð¹ ÑÐºÑ€Ð¾Ð»Ð»
  return document.body.scrollHeight > document.body.clientHeight;
}

export function showBodyScroll() {
  document.body.classList.remove('no-scroll');

  document.body.style.position = '';
  document.body.style.width = '';
  document.body.style.top = '';
  window.scroll(0, windowScrollTop);
}
