export function initFormFields() {

  let inputs = document.getElementsByTagName('input'),
    textareas = document.getElementsByTagName('textarea');

  inputs = Array.from(inputs);
  textareas = Array.from(textareas);

  inputs.forEach(element => {
    element.classList.remove('init');
    element.classList.add('init');
  });

  textareas.forEach(element => {
    element.classList.remove('init');
    element.classList.add('init');
  });

  // inputs.forEach(element => {
  //   if (element.classList.contains('init')) {
  //     element.classList.remove('init');
  //   }
  //   else {
  //     element.classList.add('init');
  //   }

  // });

  // textareas.forEach(element => {
  //   if (element.classList.contains('init')) {
  //     element.classList.remove('init');
  //   }
  //   else {
  //     element.classList.add('init');
  //   }
  // });
}

initFormFields();

document.addEventListener('focusin', (e) => {

  if (e.target.classList.contains('init')) {
    e.target.classList.remove('init');
  }
})


const regexpName = /^[Ð-Ð¯Ð°-ÑÐÑ‘A-Za-z]{1}[Ð-Ð¯Ð°-ÑÐÑ‘A-Za-z-]*[\s][Ð-Ð¯Ð°-ÑÐÑ‘A-Za-z]{1}[Ð-Ð¯Ð°-ÑÐÑ‘A-Za-z-]*([Ð-Ð¯Ð°-ÑÐÑ‘A-Za-z-\s])*$/,
  regexpEmail = /^[\w]{1}[\w-\.]*@[\w-]+\.[a-z]{2,4}$/i,
  regexpCode = /^[+]{1}\d{1,5}$/,
  regexpNumber = /^[\d]{3,7}$/;

function validateFormFields(field) {
  if (field.getAttribute('name') === 'name') {

    return [regexpName.test(field.value), 'Ð’Ð²ÐµÐ´Ð¸Ñ‚Ðµ Ð´Ð°Ð½Ð½Ñ‹Ðµ Ð² Ñ„Ð¾Ñ€Ð¼Ð°Ñ‚Ðµ Ð¤Ð°Ð¼Ð¸Ð»Ð¸Ñ Ð˜Ð¼Ñ ÐžÑ‚Ñ‡ÐµÑÑ‚Ð²Ð¾'];
  }
  else if (field.getAttribute('name') === 'email') {

    return [regexpEmail.test(field.value), 'Ð’Ð²ÐµÐ´Ð¸Ñ‚Ðµ ÑÐ».Ð¿Ð¾Ñ‡Ñ‚Ñƒ Ð² Ñ„Ð¾Ñ€Ð¼Ð°Ñ‚Ðµ email@domen'];
  }
  else if (field.getAttribute('name') === 'phone-pre') {

    return [regexpCode.test(field.value), 'Ð’Ð²ÐµÐ´Ð¸Ñ‚Ðµ ÐºÐ¾Ð´ ÑÑ‚Ñ€Ð°Ð½Ñ‹ Ð² Ñ„Ð¾Ñ€Ð¼Ð°Ñ‚Ðµ +7'];
  }
  else if (field.getAttribute('name') === 'phone-code') {

    return [regexpNumber.test(field.value), 'ÐšÐ¾Ð´ Ð´Ð¾Ð»Ð¶ÐµÐ½ ÑÐ¾Ð´ÐµÑ€Ð¶Ð°Ñ‚ÑŒ Ñ‚Ð¾Ð»ÑŒÐºÐ¾ Ñ†Ð¸Ñ„Ñ€Ñ‹'];
  }
  else if (field.getAttribute('name') === 'phone-number') {

    return [regexpNumber.test(field.value), 'ÐÐ¾Ð¼ÐµÑ€ Ñ‚ÐµÐ»ÐµÑ„Ð¾Ð½Ð° Ð´Ð¾Ð»Ð¶ÐµÐ½ ÑÐ¾Ð´ÐµÑ€Ð¶Ð°Ñ‚ÑŒ Ñ‚Ð¾Ð»ÑŒÐºÐ¾ Ñ†Ð¸Ñ„Ñ€Ñ‹'];
  }

}

document.addEventListener("input", function (e) {
  let matchArr = validateFormFields(e.target);
  if (matchArr === undefined) return;

  if (!matchArr[0]) {
    e.target.setCustomValidity(matchArr[1]);
  }
  else {
    e.target.setCustomValidity('');
  }
});
